<template>
  <div>
    <HeatMapView :heat-map="heatmap"></HeatMapView>
  </div>
</template>
<script>
import HeatMapView from "../../components/area/HeatMapView.vue";

//initial canvas size
// const sceneWidth = 900;
// const sceneHeight = 450;

export default {
  name: "HeatMap",
  components: { HeatMapView },
  data() {
    return { heatmap: null };
  },
  async created() {
    this.$ren.dashboardApi
      .getHeatMap(this.$route.params.id)
      .then((heatmap) => {
        this.heatmap = heatmap;
      })
      .catch((e) => {
        this.$toast.add({
          severity: "error",
          summary: this.$t("view.toast.get_heatmap", {
            label: this.label,
            error: e,
          }),
          life: 3000,
        });
      });
  },
  mounted() {},
  methods: {},
};
</script>

<style lang="scss"></style>
